import React from 'react';
import './App.css';
import { airplane, Cell, Board, caseChoice, emptyBoard, getBoardSize, placeItemOnBoard, rotateBoard, overlayItemOnBoard, mapBoard, Placement, randomBoard, bomb, gameover, Player, View } from './Concepts';

function ShowBoard(
  board: Board<Cell>,
  handleClick: (i: number, j: number) => void = (_1, _2) => { return },
  handleHover: (i: number, j: number) => void = (_1, _2) => { return }) {
  return (
    <div className="Board">
      <table>
        <tbody>
          <>
            {board.map((row, i) => {
              return (<tr>
                <>
                  {row.map((cell, j) => {
                    return (
                      <td
                        className={
                          "airplane" + "-" + cell.placement +
                          (cell.overlay ? " overlay" : "") +
                          (cell.bomb ? " bomb" : "")}
                        onClick={() => handleClick(i, j)}
                        onMouseEnter={() => handleHover(i, j)}>
                        {" "}
                      </td>
                    )
                  })}
                </>
              </tr>)
            })}
          </>
        </tbody>
      </table>
    </div>
  )
}

type SetupState = {
  kind: "setup"
  global: Board<Cell>
  count: number,
  hover?: { i: number, j: number }
}
type FightingState = {
  kind: "fighting",
  ourBoard: Board<Cell>
  theirBoard: Board<Cell>
  theirPlayer: Player
}
type EndState = {
  kind: "end"
  winner: "us" | "them"
  ourBoard: Board<Cell>
  theirBoard: Board<Cell>
}
type AppState = (SetupState | FightingState | EndState) & { totalCount: number, item: Board<Cell> }
class App extends React.Component<{}, AppState> {
  constructor(props: {}) {
    super(props);
    this.state = this.initState()
  }
  initState(): AppState {
    const totalCount = 3;
    return {
      kind: "setup",
      global: emptyBoard,
      item: airplane,
      count: totalCount,
      totalCount: totalCount,
      hover: undefined
    }
  }
  render() {
    const currentState = this.state
    if (currentState.kind === "setup") {
      const handleClick = (i: number, j: number): void => {
        if (currentState.count > 0) {
          const size = getBoardSize(currentState.item);
          i = i - Math.floor(size.nrow / 2)
          j = j - Math.floor(size.ncol / 2)
          caseChoice(
            placeItemOnBoard(currentState.item, currentState.global, i, j),
            (global) => this.setState({ ...currentState, global, count: currentState.count - 1 }),
            (_) => { }
          )
        }
      }
      const handleHover = (i: number, j: number): void => {
        this.setState({ ...currentState, hover: { i, j } })
      }
      const computePreviewBoard = (i: number, j: number) => {
        const size = getBoardSize(currentState.item);
        i = i - Math.floor(size.nrow / 2)
        j = j - Math.floor(size.ncol / 2)
        const previewItem = mapBoard(currentState.item, (i, j, cell) => {
          if (cell.placement === Placement.Head || cell.placement === Placement.Body) {
            return { overlay: true };
          } else {
            return { overlay: false };
          }
        })
        return overlayItemOnBoard(previewItem, currentState.global, i, j)
      }
      const startFighting = () => {
        this.setState({
          ...currentState,
          kind: "fighting",
          ourBoard: currentState.global,
          theirBoard: randomBoard(currentState.totalCount, currentState.item),
          theirPlayer: new Player(currentState.totalCount, currentState.item)
        })
      }
      let board = currentState.global;
      if (currentState.hover) {
        board = computePreviewBoard(currentState.hover.i, currentState.hover.j)
      }
      return (<div>
        {ShowBoard(board, handleClick, handleHover)}
        <div onClick={() => { this.setState({ ...currentState, item: rotateBoard(currentState.item) }) }}>
          {ShowBoard(currentState.item)} Ã— {currentState.count}
        </div>
        <button disabled={currentState.count > 0} onClick={(_) => startFighting()}>Start</button>
      </div>)
    } else if (currentState.kind === "fighting") {
      const handleDropBomb = (i: number, j: number) => {
        if (currentState.theirBoard[i][j].bomb) {
          return
        }
        const theirBoard = overlayItemOnBoard(bomb, currentState.theirBoard, i, j)
        if (gameover(theirBoard)) {
          this.setState({
            ...currentState, theirBoard, kind: "end", winner: "us"
          })
        } else {
          const { i, j } = currentState.theirPlayer.nextStep(viewBoard(currentState.ourBoard))
          const ourBoard = overlayItemOnBoard(bomb, currentState.ourBoard, i, j)
          if (gameover(ourBoard)) {
            this.setState({
              ...currentState, theirBoard, ourBoard, kind: "end", winner: "them"
            })
          } else {
            this.setState({
              ...currentState,
              theirBoard,
              ourBoard
            })
          }
        }
      }
      return (
        <div>
          {ShowBoard(currentState.ourBoard)}
          <div>-----------------------</div>
          <div className="their">
            {ShowBoard(currentState.theirBoard, (i, j) => handleDropBomb(i, j))}
          </div>
        </div>
      )
    } else if (currentState.kind === "end") {
      const message = { "us": "You won!", "them": "You lost!" }[currentState.winner];
      return (
        <div>
          {message}
          <button onClick={(_) => this.setState(this.initState())}>Restart</button>
          {ShowBoard(currentState.ourBoard)}
          <div>-----------------------</div>
          {ShowBoard(currentState.theirBoard)}
        </div>
      )
    }

  }
}


