export default App;

function viewBoard(ourBoard: Board<Cell>): Board<View> {
  return mapBoard(ourBoard, (i, j, cell) => {
    if (cell.bomb) {
      if (cell.placement === Placement.Head) {
        return View.HitHead
      } else if (cell.placement === Placement.Body) {
        return View.HitBody
      } else {
        return View.HitNone
      }
    } else {
      return View.Unknown
    }
  })
}
