export type Board<Cell> = Cell[][]

export interface Cell {
    placement?: Placement
    bomb?: boolean;
    overlay?: boolean;
}

export enum Placement {
    None = "none",
    Head = "head",
    Body = "body"
}

export const boardSize = 10
export const emptyBoard: Board<Cell> = makeEmpty(boardSize, boardSize, { placement: Placement.None })


export const airplane: Board<Cell> = [
    [{ placement: Placement.None }, { placement: Placement.Head }, { placement: Placement.None }],
    [{ placement: Placement.Body }, { placement: Placement.Body }, { placement: Placement.Body }],
    [{ placement: Placement.None }, { placement: Placement.Body }, { placement: Placement.None }],
    [{ placement: Placement.Body }, { placement: Placement.Body }, { placement: Placement.Body }],
]
export const bomb: Board<Cell> = [[{ bomb: true }]]

function makeEmpty<A>(nrow: number, ncol: number, x: A): Board<A> {
    return new Array(nrow).fill(null).map((row) => {
        return new Array(ncol).fill(copy(x))
    })
}

export function mapBoard<A, B>(oldBoard: Board<A>, todo: (i: number, j: number, cell: A) => B): Board<B> {
    return oldBoard.map((row, i) => row.map((cell, j) => todo(i, j, cell)));
}
export function summarizeBoard<A, R>(oldBoard: Board<A>, base: R, todo: (i: number, j: number, cell: A, base: R) => R): R {
    let r: R = base
    mapBoard(oldBoard, (i, j, cell) => {
        r = todo(i, j, cell, r)
    })
    return r;
}

export function rotateBoard(boardOld: Board<Cell>) {
    const sizeOld = getBoardSize(boardOld);
    const boardNew: Board<Cell> = makeEmpty(sizeOld.ncol, sizeOld.nrow, { placement: Placement.None });
    mapBoard(boardNew, (i, j, _) => {
        // x1    , y1     -> x2    , y2
        // 0     , 0      -> 0     , nrow-1,
        // nrow-1, 0      -> 0     , 0
        // 0     , ncol-1 -> ncol-1, nrow-1
        // x2 = y1
        // y2 = nrow - 1 - x1
        // x1 = nrow - 1 - y2
        // y1 = x2
        boardNew[i][j] = boardOld[sizeOld.nrow - 1 - j][i]
    })
    return boardNew
}

export function getBoardSize<A>(board: Board<A>) {
    const nrow = board.length;
    const ncol = board[0].length;
    return { nrow, ncol }
}

function copy<A>(x: A) {
    return JSON.parse(JSON.stringify(x))
}

type Choice<A, B> = { choice: true, datum: A } | { choice: false, datum: B }
function Yes<A>(x: A): { choice: true, datum: A } {
    return { choice: true, datum: x }
}
function No<B>(x: B): { choice: false, datum: B } {
    return { choice: false, datum: x }
}
export function caseChoice<A, B, C>(choice: Choice<A, B>, ifYes: (x: A) => C, ifNo: (x: B) => C) {
    if (choice.choice) {
        return ifYes(choice.datum)
    } else {
        return ifNo(choice.datum)
    }
}

export function orElse<A, B>(choice: Choice<A, B>, fallback: A) {
    if (choice.choice) {
        return choice.datum
    } else {
        return fallback
    }
}

export function placeItemOnBoard(item: Board<Cell>, global: Board<Cell>, xBase: number, yBase: number): Choice<Board<Cell>, string> {
    global = copy(global);
    const itemSize = getBoardSize(item);
    const globalSize = getBoardSize(global);
    for (let xOffset = 0; xOffset < itemSize.nrow; xOffset++) {
        for (let yOffset = 0; yOffset < itemSize.ncol; yOffset++) {
            if (item[xOffset][yOffset].placement !== Placement.None) {
                const xFocused = xBase + xOffset;
                const yFocused = yBase + yOffset;
                if (0 <= xFocused && xFocused < globalSize.nrow &&
                    0 <= yFocused && yFocused < globalSize.ncol) {
                    if (global[xFocused][yFocused].placement === Placement.None) {
                        global[xFocused][yFocused].placement = item[xOffset][yOffset].placement
                    } else {
                        return No("overlap!")
                    }
                } else {
                    return No("out of range")
                }
            } else {
                continue;
            }
        }
    }
    return Yes(global)
}


export function overlayItemOnBoard(item: Board<Cell>, global: Board<Cell>, xBase: number, yBase: number) {
    global = copy(global);
    const itemSize = getBoardSize(item);
    const globalSize = getBoardSize(global);
    for (let xOffset = 0; xOffset < itemSize.nrow; xOffset++) {
        for (let yOffset = 0; yOffset < itemSize.ncol; yOffset++) {
            const xFocused = xBase + xOffset;
            const yFocused = yBase + yOffset;
            if (0 <= xFocused && xFocused < globalSize.nrow &&
                0 <= yFocused && yFocused < globalSize.ncol) {
                global[xFocused][yFocused] = { ...global[xFocused][yFocused], ...item[xOffset][yOffset] }
            }
        }
    }
    return global
}

export function randomBoard(totalCount: number, item: Board<Cell>, board:Board<Cell> =emptyBoard): Board<Cell> {
    for (let remained = totalCount; remained > 0; remained--) {
        let updated = false
        while (!updated) {
            const numRotation = Math.floor(Math.random() * 4)
            new Array(numRotation).fill(null).forEach((_) => {
                item = rotateBoard(item);
            })
            const x = Math.floor(Math.random() * boardSize)
            const y = Math.floor(Math.random() * boardSize)
            caseChoice(
                placeItemOnBoard(item, board, x, y),
                (newBoard) => {
                    updated = true;
                    board = newBoard
                },
                (_) => { }
            )
        }
    }
    return board
}



export function gameover(ourBoard: Board<Cell>) {
    return !summarizeBoard(ourBoard, false, (i, j, cell, base) => {
        const survived = (!cell.bomb && cell.placement === Placement.Head)
        return base || survived
    })
}

