export enum View { Unknown = "unknown", HitHead = "head", HitBody = "body", HitNone = "none" }


function boardMatchView(board: Board<Cell>, theirView: Board<View>) {
    return summarizeBoard(
        theirView,
        true,
        (i, j, view, base) => {
            let matched: boolean;
            if (view === View.HitBody) {
                matched = board[i][j].placement === Placement.Body
            } else if (view === View.HitHead) {
                matched = board[i][j].placement === Placement.Head
            } else if (view === View.HitNone) {
                matched = board[i][j].placement === Placement.None
            } else {
                matched = true;
            }
            return base && matched;
        })
}

export class Player {
    totalCount: number
    item: Board<Cell>
    explanations: Board<Cell>[]
    constructor(totalCount: number, item: Board<Cell>) {
        this.totalCount = totalCount
        this.item = item
        this.explanations = []
    }
    generateExplanation(explanations: Board<Cell>[], theirView: Board<View>) {
        const totalCount = this.totalCount
        const item = this.item
        const moreExplanations = []
        for (let i = 0; i < 100; i++) {
            const board = randomBoard(totalCount, item)
            if (boardMatchView(board, theirView)) {
                moreExplanations.push(board)
            }
        }
        return moreExplanations
    }
    nextStep(theirView: Board<View>) {
        const { nrow, ncol } = getBoardSize(theirView)
        this.explanations = this.explanations.filter((board) => {
            return boardMatchView(board, theirView)
        })
        this.explanations = [...this.explanations, ...this.generateExplanation(this.explanations, theirView)]
        const countBoard = makeEmpty(nrow, ncol, 0);
        mapBoard(theirView, (i, j, cell) => {
            if (cell === View.Unknown) {
                countBoard[i][j] += 1;
            }
        })
        for (const board of this.explanations) {
            mapBoard(board, (i, j, cell) => {
                if (cell.placement === Placement.Head && theirView[i][j] === View.Unknown) {
                    countBoard[i][j] += 1;
                }
            })
        }
        const maxCount: number = summarizeBoard(
            countBoard,
            0,
            (i, j, cell, base) => {
                if (cell > base) {
                    return cell;
                } else {
                    return base
                }
            }
        )
        const candidates: {i:number, j:number}[] = []
        mapBoard(
            countBoard,
            (i, j, cell) => {
                if (cell === maxCount) {
                    candidates.push({i, j})
                }
                return
            }
        )
        return candidates[Math.floor(Math.random() * candidates.length)]
    }
}
